*{
  padding: 0;
  margin: 0;
  overflow-x: hidden;
}
#sidebarDiv {
  height: 100vh;
  border-right: 0.5px solid rgba(0, 0, 0, 0.25);
  width: 230px;
  padding-top: 20px;
  box-sizing: border-box;
}
#sidebarDiv ul {
  list-style: none;
  margin-left:20px;
  margin-right:20px;
}

#sidebarDiv ul li{
 padding:8px;
 color:rgb(69, 183, 228);
 padding-top:20px;
 padding-bottom:20px;
 display: flex;
}
#sidebarDiv ul li img{
  margin-top:-3px;
  margin-right:4px;
   }
#sidebarDiv ul li:hover{
background-color: rgb(69, 183, 228 , 0.1)
 }
.active{
  background-color: rgb(69, 183, 228 , 0.1)
   }

#navdiv {
    display: flex;
  height: 70px;
  border-bottom: 0.5px solid rgba(0, 0, 0, 0.25);
}

#navdiv h2{
  height: 45px;
  text-align: center;
  padding-top: 25px;
  margin:0 5vw 0 40px;
  color:rgb(69, 183, 228);
}
#flexDiv {
  display: flex;
}
#renderComponent {
  margin-left: 30px;
}
#titleDiv{
    padding-top: 23px;
    padding-left:20px;
    font-size: 1.5rem;
    color:#12121290
}
#menu{
    position:absolute;
     width:25px; 
     margin:20px 0px 10px 20px;
     right: 20px;
     top:4px;
     display: none;
 }

 #sideDiv{
    width:100%;
    background-color: #D6D7DC;
    position:fixed;
    transition: all .5s;
    top:0px;
    width:0;
    height:100vh;
    flex-direction: column;
    padding-top:150px;
    font-weight:600;
    font-size: 20px;
    z-index: 2;
}
#sideDiv ul li{
  padding:8px;
  color:rgb(69, 183, 228);
  padding-top:20px;
  padding-bottom:20px;
  display: flex;
  width:50vw;
  margin-left:20px;
 }
 #sideDiv ul li img{
   margin-top:-3px;
   margin-right:4px;
    }
 #sideDiv ul li:hover{
 background-color: rgb(69, 183, 228 , 0.1)
  }

#crossimg{
    position:absolute;
    top:20px;
    right:20px;
}
#homecontent{
margin:40px 30px 0 0;
width:65vw;
}
#homecontent ul{
  margin-left:30px;
  }
@media (max-width: 860px) {
  #sidebarDiv {
    width: 26.8%;
  }
}
@media (max-width: 630px) {
    #sidebarDiv {
display: none;
      }
      #menuDiv {
        display: none;
      }
      #menu{
        display: block;
      }
      #homecontent{
        width:85vw;
        }
        #navdiv h2{
          margin-left:30px;
        }
}
@media (max-width: 340px) {
  #homecontent{
    width:80vw;
    }
}